.Modal {
    z-index: 3;
    position: fixed;
    overflow: hidden;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: #11111188;
    display: none;
    align-items: center;
    justify-content: center;

    &.show {
        display: flex;
    }

    & .inner {
        flex: 0 0 auto;
        flex-wrap: nowrap;
        overflow-y: hidden;
        background-color: #000;
        max-width: 600px;
        max-height: 500px;
        width: 100%;
        height: 100%;
        border: 2px solid #FFF;
        border-radius: 10px;

        & a {
            color: #FFF;
        }

        & .title {
            display: flex;
            height: 30px;
            line-height: 30px;
            padding: 5px;

            & span {
                flex: 1 0 0px;
                font-size: 18px;
                text-align: center;
                margin-left: 30px;
            }

            & div {
                flex: 0 0 30px;
                border-radius: 10px;
                text-align: center;
                cursor: pointer;
                background-color: #CC0000;
            }
        }

        & .content {
            flex: 1 1 0px;
            padding: 5px;
            overflow-y: auto;
            max-height: calc(100% - 50px);

            & .button {
                background-color: #FFF;
                color: #000;
                border-radius: 3px;
                padding: 0px 5px;
                cursor: pointer;
                user-select: none;
            }

            & .italic {
                font-style: italic;
            }

            & .lred {
                color: #FFCCCC;
            }
        }
    } 
}